import React, { useEffect, useState } from 'react';

import { useHistory } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Input, Switch, Button } from "@material-ui/core";
import { saveMessage } from "../Main/Model/MessageModel";

const Admin = () => {
	const history = useHistory();
	const { register, handleSubmit, watch, errors } = useForm();
	const saveFunction = saveMessage();

	const onSubmit = (data: any) => {
		saveFunction(data.message, data.onOff);
	};

	useEffect(() => {
		const key = prompt("What is the key of admin?");
		if (key !== process.env.REACT_APP_ADMIN_KEY) {
			alert("You are not Admin, Sorry.");
			history.push("/");
		}
	}, []);

	return (
		<>
			<form onSubmit={handleSubmit(onSubmit)}>
				<Input name="message" defaultValue="test" inputRef={register({ required: true })} />
				<Switch name="onOff" inputRef={register({ required: true })} />

				{errors.exampleRequired && <span>This field is required</span>}
				<Button type="submit">SAVE</Button>
			</form>
		</>
	);
};

export default Admin;
