import React from 'react';
import clsx from 'clsx';
import axios from "axios";
import { useHistory } from "react-router-dom";

import { Grid, Slide, Toolbar, IconButton, useScrollTrigger } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

import { useUserDispatch } from '../Main/Model/UserModel';

type HeaderProps = {
	open: boolean;
	useOpen: React.Dispatch<boolean>;
	userName: string;
	userType: string;
}

const Header = ({ open, useOpen, userName, userType }: HeaderProps) => {
	const trigger = useScrollTrigger();
	const setUser = useUserDispatch();
	const history = useHistory();

	const Logout = () => {
		const confirm = window.confirm("정말로 로그아웃 하시겠습니까?");

		if (confirm) {
			axios.post(`${process.env.REACT_APP_SERVER}/logout`, { withCredentials: true })
				.then(res => {
					setUser(undefined);
					history.push("/");
				})
				.catch(err => {
					alert("로그아웃을 할 수 없었습니다.");
					history.push("/");
				});
		}
	}

	return (
		<Slide appear={false} direction="down" in={!trigger}>
			<Grid className={clsx(open ? "small-header" : "wide-header", "header")}>
				<Grid className="tool-bar">
					{
						!open &&
						<IconButton
							className="open-sidebar"
							aria-label="open drawer"
							onClick={() => useOpen(true)}
							edge="start"
						>
							<MenuIcon />
						</IconButton>
					}
					<Grid className="header-menu-con">
						<Grid className="name-field"><p className="name">{userName}</p>{userType === 'professor' ? "교수" : ""}<p>님, 환영합니다.</p></Grid>
						<Grid className="menu-field" onClick={Logout}>
							<ExitToAppIcon />
							<p>로그아웃</p>
						</Grid>
					</Grid>
				</Grid>
			</Grid>
		</Slide>
	);
}

export default Header;