import React, { useEffect, useState, createRef } from 'react';

import { Grid, Toolbar, Badge } from "@material-ui/core";
import { Alert, AlertTitle } from '@material-ui/lab';
import MailIcon from '@material-ui/icons/Mail';
import CancelIcon from '@material-ui/icons/Cancel';

import { Header, SideBar } from '.';
import { useUserState } from '../Main/Model/UserModel';
import { useOnOffState, useMessageState } from '../Main/Model/MessageModel';

const Layout: React.FC = ({ children }) => {
	const [open, useOpen] = useState<boolean>(true);
	const [alertOpen, setAlertOpen] = useState<boolean>(false); // badge를 누를 때 alert가 3초간 오픈 되도록
	const [clearTime, setClearTime] = useState<NodeJS.Timeout | undefined>(undefined);

	const onOff = useOnOffState();
	const message = useMessageState();

	const user = useUserState();
	const mobileRef = createRef<HTMLDivElement>();

	const onAlert = async () => {
		// 5초 후에 꺼짐
		await setAlertOpen(true);
		await setClearTime(setTimeout(() => setAlertOpen(false), 3000000));
	};

	const handleCloseAlert = async () => {
		// 수동으로 껐을 때, timeOut도 해지
		if (clearTime)
			await clearTimeout(clearTime);
		await setAlertOpen(false);
		await setClearTime(undefined);
	}

	useEffect(() => {
		// routing 변경 시마다 open 해지 => 모바일 떄문에
		if (mobileRef.current && mobileRef.current.offsetWidth < 768) {
			// 모바일이면 페이지 이동 시마다 sidebar 닫기
			useOpen(false);
		}
	}, [window.location.href]);

	return (
		<Grid id="page-con">
			<div className="mobile-check" ref={mobileRef} />
			{
				user &&
					<>
						<SideBar
							classes={open ? "open-sidebar" : "close-sidebar"}
							useOpen={useOpen}
							userType={user.type}
						/>
						<Grid id="wrap">
							<Header
								open={open}
								useOpen={useOpen}
								userName={user.userName}
								userType={user.type}
							/>
							<Toolbar className="back-margin" />
							<Grid className="content-wrap">
								{children}
							</Grid>
						</Grid>
						{
							onOff &&
							<>
								{
									alertOpen ?
										<Alert id="alert" severity="info">
											<AlertTitle className="title">
												공지사항
												<CancelIcon color="primary" onClick={handleCloseAlert} />
											</AlertTitle>
											<p className="info">{message}</p>
										</Alert> :
										<Badge id="badge" color="secondary" variant="dot" onClick={onAlert}>
											<MailIcon />
										</Badge>
								}
							</>
						}
					</>
			}
		</Grid>
	);
};

export default Layout;